$borderColor: salmon;

.third-title {
  text-decoration: underline;
  border: 1px solid $borderColor;
}

.stock-alert {
  background-color: #eee;
  padding: 20px;
  border-radius: 5px;
  max-width: 700px;
  width: 100%;
  margin: 0 auto;
}

.stock-alert p {
  margin: 0;
}

.stock-alert-green {
  @extend .stock-alert;
  background-color: #008000;
}
